:root {
  --doc-font-family: serif;
  --doc-font-size: 9pt;
  --doc-math-font-size: .9em;
  --doc-math-font-weight: regular;

  --ui-font-family: 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Ubuntu', 
    'Source Sans Pro', 'Open Sans', sans-serif;
  --ui-font-family-monospace: "Inconsolata", "Consolas", 
    "Menlo", "Ubuntu Mono", "Roboto Mono", "Courier", monospace;
  --ui-font-size: 3mm;

  --edit-color-dark: rgba(0,150,100,1);
  --edit-color-light: rgba(50,200,150,0.7);
  --edit-color-faded: rgba(100,250,150,0.01);
}
@page { margin: 0 }
body { margin: 0 }


/** For screen preview **/
@media screen {
  html, body { background-color: #aaa; }
  .page-outer {
    background: white;
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.3);
    margin: 3mm auto;
  }
  .page-overflowing {
    outline: 4px dotted red;
  }
}

@media print {
  .document {
    position: absolute;
  }
  .page-outer {
    page-break-inside: avoid;
    page-break-after: always;
    overflow: hidden;
  }
}
.katex-html {
  white-space: nowrap;
  font-size: 0.9em;
  font-size: var(--doc-math-font-size, 0.9em);
  font-weight: normal;
  font-weight: var(--doc-math-font-weight, normal);
}


.document blockquote {
  font-style: italic;
}

.document em > strong {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

/* TABLES */

.document table {
  border-collapse: collapse;
}

.document th {
  font-weight: bold;
}

.document th, .document td {
  border: 1px solid black;
  padding: 0.2em 0.5em;
}

.document {
  font-family: serif;
  font-family: var(--doc-font-family, serif);
  font-size: 9pt;
  font-size: var(--doc-font-size, 9pt);
  counter-reset: question 0;
  counter-reset: question var(--doc-startnumber, 0);
  display: flex;
  flex-direction: column;
}

.answerchoice-list {
  list-style: none;
  padding: 0;
}

.answerchoice-item {
  margin: 2mm;
}

.answerchoice-item:before {
  counter-increment: answerchoice 1;
  content: counter(answerchoice, lower-alpha) ') ';
  margin-right: 2mm;
}

.answerkey {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.1em;
  counter-reset: answerkey 0;
  counter-reset: answerkey var(--doc-startnumber, 0);
  margin: 0;
  padding: 0;
  position: relative;
  max-height: 90%;
}

.answerkey-item {
  margin: 1mm;
}

.answerkey-item::before {
  counter-increment: answerkey 1;
  content: counter(answerkey) '. ';
}

.blank-underline {
  display: inline-block;
  border-bottom: 1px solid black; 
  margin: 0 1mm;
}

/* .blank-underline-heading */

.editable > *:hover {
  outline: 3px dotted lightgreen;
  outline: 3px dotted var(--edit-color-light, lightgreen);
  background-color: var(--edit-color-faded);
}

.error-failed-render {
  color: red;
}

.header {
  border-collapse: collapse;
  width: 100%;
}

.header td {
  border: none;
  font-size: 1.4em;
  font-weight: bold;
  border: none;
  padding-bottom: 3mm;
  text-align: left;
}

.header td:last-child {
  text-align: right;
  right: 0;
}

.header-empty {
  height: 5mm;
}

.list-auto {
  list-style-type: upper-roman;
}

.question-wrapper {
  display: flex;
  margin: 1em 0;
}

.question-wrapper::before {
  counter-increment: question 1;
  counter-reset: answerchoice 0;
  content: counter(question) '. ';
  font-size: 1em;
  position: relative;
}

:root {
  --layout-answer-diagram: 'answer diagram'
}

.question-layout {
  display: grid;
  padding: 0;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
  width: 100%;
}

.question-layout {
  grid-template-areas: 
    'instructions instructions'
    'question question'
    'answer diagram';
  grid-template-areas: 
    'instructions instructions'
    'question question'
    var(--layout-answer-diagram);
}

.question-wrapper > .question-layout {
  margin-left: 1em;
}

.question-area {
  margin-bottom: 0.5em;
  margin-right: 0.5em;
}

:root {
  --plot-path-color: #c07;
  --plot-asymptote-color: #777;
  --plot-axis-color: #222;
  --plot-grid-color: #89a;
  --plot-path-width: 0.5mm;
  --plot-axis-width: 0.4mm;
  --plot-grid-width: 0.2mm;
}

.plot-cartesian-plane {
  min-height: 0.5in;
  min-width: 0.5in;
}

.plot-path {
  stroke: #c07;
  stroke: var(--plot-path-color);
  stroke-width: 0.5mm;
  stroke-width: var(--plot-path-width);
  stroke-linejoin: round;
  fill: none;
  stroke-linecap: round;
}

.plot-asymptote {
  stroke: #777;
  stroke: var(--plot-asymptote-color);
  stroke-width: calc(1.5 * 0.5mm);
  stroke-width: calc(1.5 * var(--plot-path-width));
  stroke-dasharray: 2mm 1mm;
  stroke-linecap: none;
}

.plot-point {
  stroke: #c07;
  stroke: var(--plot-path-color);
  fill: #c07;
  fill: var(--plot-path-color);
  stroke-width: calc(3 * 0.5mm);
  stroke-width: calc(3 * var(--plot-path-width));
  stroke-linecap: round;
}

.plot-hole-exterior {
  stroke: #c07;
  stroke: var(--plot-path-color);
  fill: #c07;
  fill: var(--plot-path-color);
  stroke-width: calc(4 * 0.5mm);
  stroke-width: calc(4 * var(--plot-path-width));
  stroke-linecap: round;
}

.plot-hole-interior {
  stroke-width: 0.5mm;
  stroke-width: var(--plot-path-width);
  stroke-width: calc(2 * 0.5mm);
  stroke-width: calc(2 * var(--plot-path-width));;
  stroke: white;
  fill: white;
  stroke-linecap: round;
}

.plot-grid-axis {
  stroke: #222;
  stroke: var(--plot-axis-color);
  stroke-width: 0.4mm;
  stroke-width: var(--plot-axis-width);
  stroke-opacity: 1;
  stroke-linecap: square;
}

.plot-grid-line {
  stroke: #89a;
  stroke: var(--plot-grid-color); 
	stroke-width: 0.2mm; 
	stroke-width: var(--plot-grid-width);
	stroke-opacity: 0.9;
}

.plot-region-shaded {
  fill: #c07;
  fill: var(--plot-path-color);
  fill-opacity: 0.2;
}
